.is-hidden {
  display: none;
}

.custom-loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #fff;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

.custom-loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid #ff3d00;
  border-left: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.breed-select {
  max-width: 200px;
  margin-top: 20px;
  margin-left: 20px;
}

.loader {
  color: #ff3d00;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding-top: 40px;
  display: flex;
}

.error {
  color: red;
}

.main-info {
  text-align: center;
  background-color: #ffdead;
  border: 1px solid #ccc;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  padding: 20px;
  display: flex;
}

.main-info img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 10px;
}

.main-info img:hover {
  transition: transform .3s;
  transform: scale(1.5);
}

.main-info h1 {
  text-shadow: 1px 1px 1px #f5f5f5, 2px 2px 1px #f5f5f5;
  margin: 10px 0;
  font-size: 24px;
}

.main-info p {
  max-width: 500px;
  margin: 0;
  font-size: 14px;
  font-style: italic;
  line-height: 1.5;
}

.main-info h2 {
  text-shadow: 1px 1px 1px #f5f5f5, 2px 2px 1px #f5f5f5;
  margin: 10px 0;
  font-size: 15px;
}

.main-info p {
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
}

